                    <el-form-item label="">
                        <el-input v-model="editChannel.proxyUrl" placeholder=": your-proxy.example.com"/>
                    </el-form-item>
                    <el-form-item label="Nitro ">
                        <el-switch v-model="editChannel.isNitro"/>
                    </el-form-item>
                </template>
                <template v-else-if="currentChannelType === 'huggingface'">
                    <el-form-item label="" prop="repo">
