<template>
    <div class="container">
        <el-header>
            <div class="header-content">
                <DashboardTabs activeTab="systemConfig"></DashboardTabs>
                <div class="header-action">
                    <el-tooltip :disabled="disableTooltip" content="" placement="bottom">
                        <font-awesome-icon icon="sign-out-alt" class="header-icon" @click="handleLogout"></font-awesome-icon>
                    </el-tooltip>
                </div>
            </div>
        </el-header>
        <SysConfigTabs
            v-model:activeIndex="activeIndex"
            v-model:isCollapse="isSidebarCollapse"
        />
        <!--  -->
        <component :is="currentComponent" :class="['main-container', { 'collapsed': isSidebarCollapse }]" />
    </div>
</template>
<script>
import DashboardTabs from '@/components/DashboardTabs.vue';
import SysConfigTabs from '@/components/SysConfigTabs.vue';
import SysCogStatus from '@/components/SysCogStatus.vue';
import SysCogUpload from '@/components/SysCogUpload.vue';
import SysCogSecurity from '@/components/SysCogSecurity.vue';
import SysCogPage from '@/components/SysCogPage.vue';
import SysCogOthers from '@/components/SysCogOthers.vue';

export default {
    name: 'SystemConfig',
    data() {
        return {
            activeIndex: 'status',
            isSidebarCollapse: false
        }
    },
    watch: {
        // 
        '$route.hash': {
            immediate: true,
            handler(newHash) {
                this.activeIndex = newHash.replace('#', '');
                window.scrollTo(0, 0); // 
            }
        },
        activeIndex(newIndex) {
            // 
            const hash = `#${newIndex}`;
            this.$router.push({ hash });
        }
    },
    components: {
        DashboardTabs,
        SysConfigTabs,
        SysCogStatus,
        SysCogUpload,
        SysCogSecurity,
        SysCogPage,
        SysCogOthers
    },
    computed: {
        disableTooltip() {
            return window.innerWidth < 768;
        },
        // 
        currentComponent() {
            const hash = this.$route.hash.replace('#', '');
            switch (hash) {
                case 'status':
                    return SysCogStatus;
                case 'upload':
                    return SysCogUpload;
                case 'security':
                    return SysCogSecurity;
                case 'page':
                    return SysCogPage;
                case 'others':
                    return SysCogOthers;
                default:
                    return SysCogStatus;
            }
        }
    },
    methods: {
        handleLogout() {
            this.$store.commit('setCredentials', null);
            this.$router.push('/adminLogin');
        },
        // 
        setDefaultHash() {
            const defaultHash = '#status'; // 
            window.location.hash = defaultHash;
            this.activeIndex = defaultHash.replace('#', '');
        },
    },
    mounted() {
        //  URL 
        if (!window.location.hash) {
            this.setDefaultHash();
        }
    },
}
</script>
