                    </el-select>
                </el-form-item>
                <el-form-item label="" v-if="settings.webDAV.uploadChannel && webdavChannelList.length > 1">
                    <el-select v-model="settings.webDAV.channelName" :disabled="settings.webDAV.fixed" placeholder="" clearable>
                        <el-option
                            v-for="ch in webdavChannelList"
                            :key="ch.name"
                            :label="ch.name"
                            :value="ch.name"
